import { observer } from "mobx-react";

import ioc from "../../../lib/ioc";



interface IGameCardProps {
    bgColor: string;
    picture: string;
    id: number,
}

export const GameCard = ({
    bgColor,
    picture,
    id
}: IGameCardProps) => {

    const styles = {
        container: {
            borderRadius: '20px',
            width: '100%',
            height: '40vh',
            backgroundColor: bgColor,
            backdropFilter: 'blur(10px)',
            border: '1px solid black',
            cursor: 'pointer',
            display: 'flex',
            justifyContent: 'center',
            // alignItems: 'center'
        },
        picture: {
            padding: '5px',
            height: '100%'
           
        }
    }

    const startGame= (id: number) => {
        console.log('action has happened')
        ioc.playService.startGame(id);
        
    }

    return (
        <div style={styles.container} onClick={() => startGame(id)}>            
            <img style={styles.picture} src={picture} alt="pic" />   
        </div>
        
    )
}

export default observer(GameCard)