import { useState } from "react"

import ioc from "../../lib/ioc"

import CloseIcon from '../../assets/close-icon.svg'
import walletIcon from '../../assets/wallet.svg'

export const NavBar = () => {

    const [isHovering, setIsHovering] = useState(false);

    const handleMouseEnter = () => {
        setIsHovering(true);
    };

    const handleMouseLeave = () => {
        setIsHovering(false);
    };

    const styles = {
        navigation: {
            height: '100vh',
            display: 'flex',
            flexDirection: "column" as "column",
            justifyContent: 'space-between',
            alignItems: 'center',
            width: '10%',
            background: 'rgba(128, 198, 237, .7)',
            webkitAppRegion: 'drag',
            webkitUserSelect: 'none',
        },
        avatar: {
            borderRadius: '100%',
            backgroundColor:'red',
            width: '88px',
            height: '88px',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',

        },


        
        innerContainer: {
            display: 'flex',
            alignItems: 'center'
        },
        pagesContainer: {
            display: 'flex',
            alignItems: 'center',
            paddingLeft: '10px',
            webkitAppRegion: 'no-drag',
        },
        home: {
            cursor: 'pointer',
            paddingRight: '10px',
        },
        play: {
            cursor: 'pointer',
        },
        exit: {
            padding: '10px',
            cursor: 'pointer',
            webkitAppRegion: 'no-drag',

        },
        exitImage: {
            width: "30px",
            transform: isHovering ? 'scale(1.2)' : '',
            transition: '.4s',

        },
        login: {
            webkitAppRegion: 'no-drag',
            paddingRight: '10px',
        },
        wallet: {
            webkitAppRegion: 'no-drag',
            paddingRight: '10px',
        },
        padding: {
            paddingRight: '10px',
        }
    }


    const goToMainPage = () => {
        ioc.routerService.push('/')
    }

    const goToPlayPage = () => {
        ioc.routerService.push('/play')
    }

    const closeWindow = () => {
        window.close()
        console.log('window close')
    }

    return (
        <div style={styles.navigation}>
            {/* <div style={styles.innerContainer}>
                <div>
                    <img src="#" alt="logo" />
                </div>
                <div style={styles.pagesContainer}>
                    <p
                        style={styles.home}
                        onClick={goToMainPage}
                    >
                        Home
                    </p>
                    <p
                        onClick={goToPlayPage}
                        style={styles.play}
                    >Play</p>
                </div>
            </div>
            <div style={styles.innerContainer}>
                <div style={styles.login}>login</div>
                <div style={styles.wallet}>wallet</div>
                <div
                    onClick={closeWindow}
                    style={styles.exit}
                >
                    <img
                        src={CloseIcon}
                        alt="esc"
                        style={styles.exitImage}
                        onMouseEnter={handleMouseEnter}
                        onMouseLeave={handleMouseLeave}
                    />
                </div>
            </div> */}
            <div style={styles.avatar}>
                avatar
            </div>
            <div>
                <img src={walletIcon} alt="wallet" />
            </div>
        </div>
    )
}

export default NavBar;